.Testimonials{
  background: #000;
  padding: 40px;
  text-align: center;
}

.heading {
  color: #fff;
  font-size: 30px;
  font-weight: 600;
  letter-spacing: 1px;
  margin-bottom: 30px;
  margin-top: 30px;
}

.card-body {
    flex: 1 1 auto;
    background: #12222d;
    min-height: 1px;
    padding: 1.25rem;
    color:#fff;
}

.card{
 width: 20rem;
}

.card-img-top{
  width: 61%;
}

.card:hover{
  opacity: 1;
  transform: scale(1.1);

}

.text-secondary{
  font-style:italic;
  
}

@media (max-width: 991px) {
  .Testimonials {
    padding: 50px;
    background: #12222d;
  }

  .card {
    justify-content: center;
    margin-top: 20px;
  }

  .card-body {
    background: #000;
    color:#fff;
  }

}
