.resume {
  padding: 100px;
  background: #000;
  color: #fff;
  text-align: center;
}

.resume h2{
  margin-top: 0;
}

.resume .btn{
  color: #fff;
  border: 2px solid #2e2e2e;
  border-radius: 47px;
  background: #f70606;
  box-shadow: -48px -48px 100px #070e12, 48px 48px 100px #000;
  color: #fff;
  cursor: pointer;
}
@media (max-width: 991px) {
    .resume{
      padding: 50px;
      height: 40vh;
      background: #12222d;
  }

  .resume .btn{
    border-radius: 47px;
    background: #272727;
    box-shadow: -48px -48px 100px #070e12, 48px 48px 100px #1d3648;
    color: #fff;
    cursor: pointer;
  }
}