.work{
  background: #000;
  padding: 40px;
  text-align: center;
}

.heading {
  color: #fff;
  font-size: 30px;
  font-weight: 600;
  letter-spacing: 1px;
  margin-bottom: 25px;
  margin-top: 25px;
}

.card-body {
    flex: 1 1 auto;
    background: #12222d;
    min-height: 1px;
    padding: 1.25rem;
}

.card{
 width: 20rem;
}

.card-img-top{
  width: 61%;
}

@media (max-width: 991px) {
  .work {
    padding: 50px;
    background: #12222d;
  }

  .card {
    justify-content: center;
    margin-top: 20px;
  }

}
