
header{
  position: fixed;
  background: #000;
  padding: 20px;
  width: 100%;
  height: 30px;
}

.left_area h2{
  margin:0;
  float: right;
  margin-bottom: 30px;
}
.left_area h3{
  color: #fff;
      float: right;
    margin-top: -38px;
  text-transform: uppercase;
  font-size: 22px;
  font-weight: 900;
}

.left_area span{
  color: #19B3D3;
}

.sidebar {
  background: #000;
  margin-top: 39px;
  padding-top: 30px;
  position: fixed;
  left: 0;
  width: 250px;
  height: 100%;
  transition: 0.5s;
  transition-property: left;
}

.sidebar .profile_image{
  width: 120px;
  height: 120px;
  border-radius: 100px;
  margin-bottom: 10px;
}

.sidebar h4{
  color: #ccc;
  margin-top: 0;
  margin-bottom: 20px;
}

.sidebar a{
  color: #fff;
  display: block;
  width: 100%;
  line-height: 60px;
  text-decoration: none;
  padding-left: 40px;
  box-sizing: border-box;
  transition: 0.5s;
  transition-property: background;
}

.sidebar a:hover{
  background: #19B3D3;
}

.sidebar i{
  padding-right: 10px;
}

label #sidebar_btn{
  z-index: 1;
  color: #fff;
  position: absolute;
  cursor: pointer;
  left: 15px;
    top: 4px;
  font-size: 20px;
  margin: 5px 0;
  transition: 0.5s;
  transition-property: color;
}

label #sidebar_btn:hover{
  color: #19B3D3;
}

#check:checked ~ .sidebar{
  left: -190px;
}

#check:checked ~ .sidebar a span{
  display: none;
}

#check:checked ~ .sidebar a{
  font-size: 20px;
  margin-left: 170px;
  width: 80px;
}

.content{
  margin-left: 0;
  background: url(../images/background.jpg) no-repeat;
  background-position: center;
  background-size: cover;
  height: 100vh;
  transition: 0.5s;
  background-color: #272727;
  display: flex;
  justify-content: center;
  align-items: center;
}

.heading {
  color: #fff;
  font-size: 30px;
  font-weight: 600;
  letter-spacing: 1px;
  margin-bottom: 10px;
}

.text {
  color: #fff;
  font-size: 16px;
  font-weight: 300;
  letter-spacing: 1px;
}

.content h2 {
  font-size: 60px;
}

.content p {
  font-size: 18px;
}

#check:checked ~ .content{
  margin-left: 250px;
}

#check:checked ~ .content h2{
  font-size: 36px;
}

#check{
  display: none;
}

.modal img{
  margin:auto;
  display: block;
  width: 80%;
  max-width: 700px;
}

.modal-body{
  text-align: center;
    font-size: 32px;
    color: #12222d;
    font-weight: 700
}

.btn-lg{
  color: #fff;
  border: 2px solid #2e2e2e;
  border-radius: 47px;
  background: #f70606;
  box-shadow: -48px -48px 100px #070e12, 48px 48px 100px #000;
  color: #fff;
  cursor: pointer;
}

.modal-footer button{
  background: #f70606;
}

@media (max-width: 991px) {
   .content {
    padding: 50px;
  }
  header {
    padding: 20px 50px;
    background: #12222d;
  }

  .sidebar{
    background: #12222d;
  }


  .btn-lg{
    border-radius: 47px;
    background: #272727;
    box-shadow: -48px -48px 100px #070e12, 48px 48px 100px #1d3648;
    color: #fff;
    cursor: pointer;
  }
}